@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
#header-nav {
    background-color: #FBC966;
    font-weight: bolder;
    /* Don't linebreak between words in an element*/ 
    white-space: nowrap;
}

#brand {
    color: #FB8974;
    /* font-weight: bold; */
    /* letter-spacing: 1px; */
}

#nav-pages {
    letter-spacing: 1px;
}

.activated {
    text-shadow: 2px 4px #90CEC7;
}
footer {
    background-color: #FBC966; 
}

div {
    color: #212529;
}
#headshot {
    box-shadow: 8px 8px #FB8974, -8px -8px #90CEC7;
    border:none;
}

#about-desc {
    line-height: 1.6;
}
#btn-resume {
    background-color: #90CEC7;
    border: none;
    color: #FB8974;
    letter-spacing: 1px;
    font-weight: bold;
}

#container-btn-resume {
    margin: 0;
    background-color: transparent;
    text-align: center;
}

h3 {
    color: #FB8974;
}

h4 {
    color: #90CEC7;
}

.skill-item {
    /* Don't linebreak between words in an element*/ 
    white-space: nowrap;
}

.skill-group {
    border-color: #FB8974;
}
#btn-contact {
    background-color: #90CEC7;
    border: none;
    color: #FB8974;
    letter-spacing: 1px;
    font-weight: bold;
}

#container-btn-contact {
    margin: 0;
    background-color: transparent;
    text-align: center;
}
/* Import google fonts to access Roboto font */

/* Set default font */
* {
  font-family: 'Roboto', sans-serif;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

